import {Component, OnInit, ViewEncapsulation} from '@angular/core';
import {Constants} from '../../../app.constants';
import {ICity, IState} from '../../../interfaces/interfaces';
import {FormBuilder, FormGroup, Validators} from '@angular/forms';
import {VendorDataService} from '../../../services/vendor-data.service';
import {Router} from '@angular/router';

@Component({
    selector: 'app-create-warehouse',
    templateUrl: './create-warehouse.component.html',
    styleUrls: ['./create-warehouse.component.scss'],
    encapsulation: ViewEncapsulation.None
})
export class CreateWarehouseComponent implements OnInit {

    public newForm: FormGroup;
    states: IState[] = [];
    cities: ICity[] = [];
    errorMessage = '';
    loading: boolean;

    constructor(private vendorDataService: VendorDataService,
                private router: Router,
                private formBuilder: FormBuilder) {
    }

    ngOnInit() {
        this.buildForm();
        this.getStates()
    }

    buildForm() {
        this.newForm = this.formBuilder.group({
            name: ['', Validators.compose([Validators.required])],
            address_line_1: ['', Validators.compose([Validators.required])],
            city_id: ['', Validators.compose([Validators.required])],
            state_id: ['', Validators.compose([Validators.required])],
            address_line_2: [''],
            pobox_number: ['']
        })
    }

    onSubmit() {
        this.loading = true;
        this.errorMessage = '';
        this.vendorDataService.addWarehouse(this.newForm.value).subscribe((resp) => {
            Constants.succeeded(resp.message);
            this.newForm.reset();
            this.loading = false;
        }, (err) => {
            Constants.handleError(err, this.router, true);
            this.loading = false;
        })
    }

    getStates() {
        this.vendorDataService.getStates().subscribe((states: IState[]) => {
                this.states = states
            }, (err) => {
                Constants.handleError(err, this.router, true);
            },
        );
    }

    changeCities(v) {
        this.cities = [];
        this.newForm.patchValue({'city_id': ''});
        if (!v) {
            return;
        }

        this.states.forEach((state) => {
            if (state.id.toString() === v.toString()) {
                this.cities = state.cities;
            }
        });
    }

}
