import {Component, OnInit, ViewEncapsulation} from '@angular/core';
import {Constants} from '../../../app.constants';
import Swal from 'sweetalert2';
import {ILocation, ILocations, IWarehouse, RVendor} from '../../../interfaces/interfaces';
import {FormBuilder, FormGroup} from '@angular/forms';
import {VendorDataService} from '../../../services/vendor-data.service';
import {VendorGoodsService} from '../../../services/vendor-goods.service';
import {Router} from '@angular/router';
import {AppData} from '../../../data';
import {MapsAPILoader, LatLng} from '@agm/core';
import {AuthService} from '../../../services/auth.service';

// import {} from '@types/googlemaps';

@Component({
    selector: 'app-warehouse-list',
    templateUrl: './warehouse-list.component.html',
    styleUrls: ['./warehouse-list.component.scss'],
    encapsulation: ViewEncapsulation.None
})
export class WarehouseListComponent implements OnInit {

    warehouses: IWarehouse[] = [];
    loading: boolean;
    searching = false;
    errorMessage = 'Search for a warehouse';
    lat = 9.442054;
    lng = 9.0262185;
    zoom = 7;
    canAddWarehouse = false;
    vendors?: RVendor[] = [];
    searchForm: FormGroup;
    oid = localStorage.getItem('oid');
    locations?: ILocations[] = [];

    constructor(private formBuilder: FormBuilder,
                private vendorDataService: VendorDataService,
                private goodsService: VendorGoodsService,
                private router: Router, private mapsApiLoader: MapsAPILoader) {
        this.canAddWarehouse = this.can(this.permissions.add_warehouses);
    }

    get permissions() {
        return AppData.permissions;
    }

    can(permission) {
        return AuthService.can(permission);
    }

    get isVendor() {
        return (Constants.getAuthenticatedUser() || {}).r === AppData.roles.vendor;
    }

    ngOnInit() {
        const config = {
            searchField: [''],
            vendor_id: ''
        };
        if (this.isVendor) {
            config.vendor_id = this.oid
        }

        this.searchForm = this.formBuilder.group(config);
        if (!this.isVendor) {
            this.errorMessage = 'Select a vendor to show list of warehouses.';
            this.loadVendors();
        } else {
            this.submitSearch();
        }
    }

    loadVendors() {
        this.goodsService.getVendors().subscribe((vendors: RVendor[]) => {
            this.vendors = vendors;
            if (this.vendors.length < 1) {
                this.errorMessage = 'No vendor available on this platform yet';
            }
        }, (err) => {
            this.errorMessage = Constants.handleError(err, this.router);
        });
    }

    // getGeocode(city: string, state: string): Promise<any> {
    //     return new Promise((resolve, reject) => {
    //         const address = city + ' ' + state + ', Nigeria';
    //         let defaultLocation: ILocation;
    //         this.mapsApiLoader.load().then(() => {
    //             const geoCoder = new google.maps.Geocoder();
    //             geoCoder.geocode({'address': address}, function (results, status) {
    //                 if (status === google.maps.GeocoderStatus.OK) {
    //                     const latLng: any = results[0].geometry.location;
    //                     defaultLocation = {
    //                         latitude: latLng.lat(),
    //                         longitude: latLng.lng()
    //                     };
    //                     resolve(defaultLocation);
    //                 } else {
    //                     defaultLocation = {
    //                         latitude: 9.442054,
    //                         longitude: 9.0262185
    //                     };
    //                     reject(defaultLocation);
    //                 }
    //             });
    //         })
    //     });
    // }

    getWarehouses(params) {
        this.searching = true;
        this.errorMessage = '';
        this.vendorDataService.getWarehousesWithParam(params).subscribe((warehouses: IWarehouse[]) => {
                this.locations = [];
                if (warehouses) {
                    this.warehouses = warehouses;
                    // warehouses.forEach((data) => {
                    // if (data.address && data.address.city && data.address.state) {
                    // this.getGeocode(data.address.state, data.address.city).then((res) => {
                    //     const d = {
                    //         longitude: res.longitude,
                    //         latitude: res.latitude,
                    //         description: data.address.city + ', ' + data.address.state + ' state'
                    //     };
                    //     this.locations.push(d);
                    // }, (err) => {
                    // });
                    // }
                    // });
                } else {
                    this.warehouses = [];
                }

                if (this.warehouses.length) {
                    this.errorMessage = '';
                } else {
                    this.errorMessage = 'No result to show';
                }

                this.searching = false;
            }, (err) => {
                this.errorMessage = Constants.handleError(err, this.router);
                this.searching = false
            }
        );
    }

    submitSearch() {
        const searchText = this.searchForm.get('searchField').value;

        this.warehouses = [];

        let param = '';
        if (searchText) {
            param += 'search=' + searchText;
        }

        if (!this.isVendor) {
            const vendor = this.searchForm.get('vendor_id').value;
            if (!vendor) {
                this.errorMessage = 'Select a vendor to search for warehouse';
                return;
            }

            if (param) {
                param += '&';
            }
            param += 'vendor=' + vendor;
        }

        if (param) {
            param = '?' + param;
        }

        this.getWarehouses(param);
    }

    deleteWarehouse(warehouse: IWarehouse, i) {
        this.errorMessage = '';
        Swal.fire({
            title: 'Confirm delete',
            text: 'Do you want to delete this warehouse?',
            type: 'question',
            cancelButtonText: 'Cancel',
            showCancelButton: true,
            confirmButtonText: 'Yes Delete',
            showConfirmButton: true
        }).then(willDelete => {
            if (willDelete.value) {
                this.vendorDataService.deleteWarehouse(warehouse.id).subscribe((response) => {
                    this.warehouses.splice(i, 1);
                    Constants.succeeded('Warehouse record deleted successfully');
                }, (err) => {
                    Constants.handleError(err, this.router, true);
                });
            }
        })
    }

}
