import {Component, OnInit, ViewEncapsulation} from '@angular/core';
import {VendorDataService} from '../../../services/vendor-data.service';
import {ActivatedRoute, Router} from '@angular/router';
import {FormBuilder, FormGroup, Validators} from '@angular/forms';
import {ICity, IState, IWarehouse, RWarehouse} from '../../../interfaces/interfaces';
import {Constants} from '../../../app.constants';

@Component({
    selector: 'app-edit-warehouse',
    templateUrl: './edit-warehouse.component.html',
    styleUrls: ['./edit-warehouse.component.scss'],
    encapsulation: ViewEncapsulation.None
})
export class EditWarehouseComponent implements OnInit {

    public updateWarehouseForm: FormGroup;
    states: IState[] = [];
    cities: ICity[] = [];
    errorMessage = '';
    loading: boolean;
    pageLoading: boolean;

    warehouse: RWarehouse = null;

    constructor(private vendorDataService: VendorDataService,
                private router: Router,
                private route: ActivatedRoute,
                private formBuilder: FormBuilder) {
    }

    ngOnInit() {
        const warehouse_id = this.route.snapshot.params['id'];
        this.getStates();
        this.getWarehouse(warehouse_id);
        this.buildForm();
    }

    buildForm() {
        this.updateWarehouseForm = this.formBuilder.group({
            name: ['', Validators.required],
            address_line_1: ['', Validators.required],
            address_line_2: [''],
            pobox_number: [''],
            state_id: [''],
            city_id: [''],
        })
    }

    getWarehouse(warehouse_id: number) {
        this.pageLoading = true;
        this.vendorDataService.getWarehouseByID(warehouse_id).subscribe((warehouse: RWarehouse) => {
            this.pageLoading = false;
            if (!warehouse) {
                return;
            }
            this.warehouse = warehouse;
            if (warehouse.address.state_id) {
                this.states.forEach((state) => {
                    if (state.id.toString() === warehouse.address.state_id.toString()) {
                        this.cities = state.cities;
                    }
                })
            }
            this.updateWarehouseForm.patchValue({
                name: this.warehouse.name,
                address_line_1: this.warehouse.address ? this.warehouse.address.address_line_1 : '',
                address_line_2: this.warehouse.address ? this.warehouse.address.address_line_2 : '',
                pobox_number: this.warehouse.address ? this.warehouse.address.pobox_number : '',
                state_id: this.warehouse.address ? this.warehouse.address.state_id : '',
                city_id: this.warehouse.address ? this.warehouse.address.city_id : ''
            });
            this.pageLoading = false;
        }, (err) => {
            this.errorMessage = Constants.handleError(err, this.router);
            this.pageLoading = false;
        })
    }

    onSubmit({value, valid}: { value: IWarehouse, valid: boolean }) {
        value.id = this.warehouse.id;
        this.loading = true;
        if (value.id) {
            this.vendorDataService.updateWarehouse(value).subscribe((warehouse: IWarehouse) => {
                if (warehouse) {
                    Constants.succeeded('Warehouse information successfully updated');
                }
                this.loading = false;
            }, (err) => {
                Constants.handleError(err, this.router, true);
                this.loading = false;
            })
        }
    }


    getStates() {
        this.vendorDataService.getStates().subscribe((states: IState[]) => {
            this.states = states
        }, (err) => {
            Constants.handleError(err, this.router, true);
        });
    }

    changeCities(v) {
        this.cities = [];
        this.updateWarehouseForm.patchValue({'city_id': ''});
        if (!v) {
            return;
        }

        this.states.forEach((state) => {
            if (state.id.toString() === v.toString()) {
                this.cities = state.cities;
            }
        });
    }

}
