import {NgModule} from '@angular/core';
import {CommonModule} from '@angular/common';
import {WarehouseListComponent} from './warehouse-list/warehouse-list.component';
import {CreateWarehouseComponent} from './create-warehouse/create-warehouse.component';
import {EditWarehouseComponent} from './edit-warehouse/edit-warehouse.component';
import {RouterModule, Routes} from '@angular/router';
import {PerfectScrollbarModule} from 'ngx-perfect-scrollbar';
import {FormsModule, ReactiveFormsModule} from '@angular/forms';
import {AgmCoreModule} from '@agm/core';
import {CustomErrorModule} from '../../shared/custom-error/custom-error.module';
import {LoaderModule} from '../../shared/loader/loader.module';

export const routes: Routes = [
    {path: 'create', component: CreateWarehouseComponent},
    {path: 'edit/:id', component: EditWarehouseComponent},
    {path: '', redirectTo: '/app/warehouses/list', pathMatch: 'full'},
    {path: 'list', component: WarehouseListComponent},
];

@NgModule({
    declarations: [WarehouseListComponent, CreateWarehouseComponent, EditWarehouseComponent],
    imports: [
        CommonModule,
        PerfectScrollbarModule,
        RouterModule.forChild(routes),
        FormsModule, CustomErrorModule, LoaderModule,
        ReactiveFormsModule,
        AgmCoreModule.forRoot({
            apiKey: 'AIzaSyApVJI8RrUJg7UsWeja6ZSq7v3g-n-fWTQ'
        })
    ]
})
export class WarehousesModule {
}
